import React, { Component } from 'react'
import logo from '../static/logo.png'

export class Landing extends Component {

	state = {
    counterPlus: 0
  }

  _addItem = (e) => {
  	var currentCounter = this.state.counterPlus
  	this.setState({
  		counterPlus: currentCounter + 1
  	})
  }

	render() {
		const { counterPlus } = this.state

		var item = []
		for (var x = 0; x < counterPlus; x++) {
			
			item.push(
				<div className='ItemCard'>
					<div className='CircleIcon'>
						<i className='icon-checkmark'></i>
					</div>
					<div className='CardDetails'>
						<div className='VerticalCenter'>
							<p>Monday</p>
							<p>10th 2:28</p>
							<p>PM</p>
						</div>
						<div className='BorderLeft VerticalCenter'>
							<i className='icon-compass'></i>
						</div>
						<div className='VerticalCenter'>
							<p>Houston, TX, 33619</p>
							<p>Atlanta, GA, 30123</p>
						</div>
						<div className='BorderLeft VerticalCenter'>
							<i className='icon-truck'></i>
						</div>
						<div className='PriceSection'>
							<p className='Price'>$250.00</p>
							<div className='CheckItemNumber'>
								<p>1</p>
							</div>
						</div>
					</div>
				</div>
			)
		}

		return(
			<div>
				<header className='Header'>
					<img
						src={logo}
						alt='logo'
						width={112}
						height={112}
					/>
					<nav className='Navigator'>
						<li><i className='icon-home3'></i><p>Home</p></li>
						<li><i className='icon-envelop'></i><p>Messages</p></li>
						<li><i className='icon-star-full'></i><p>Wishlist</p></li>
						<li><i className='icon-cog'></i><p>Settings</p></li>
						<li><i className='icon-user'></i><p>My Account</p></li>
					</nav>
				</header>

				<div className='Container'>
					<section className='LeftSide'>
					  <p>Smart Filters</p>
					  
					  <hr/>

					  <div className='CheckersGroup'>
						  <div className='Checkers'>
							  <i className='icon-price-tag'></i>
								<label className='switch'>
								  <input type='checkbox' />
								  <span className='slider round'></span>
								</label>
						  </div>
						  <div className='Checkers'>
							  <i className='icon-calendar'></i>
								<label className='switch'>
								  <input type='checkbox' />
								  <span className='slider round'></span>
								</label>
						  </div>
						  <div className='Checkers'>
							  <i className='icon-heart'></i>
								<label className='switch'>
								  <input type='checkbox' />
								  <span className='slider round'></span>
								</label>
						  </div>

						  <hr/>

						  <p>Status</p>

						  <div className='StatusGroup'>
							  <input
						    	type='text'
						    	className='StatusElements'
						    	onChange={this._handleChange}
						    	placeholder='Search'
						    />
						    <button className='StatusElements'>DELIVERED</button>
						  </div>

					  </div>
					</section>
					<section className='RightSide'>
						<vr />

						<div className='ItemSearch'>
							<div className='CircleIcon'>
								<i className='icon-checkmark'></i>
							</div>
							<input
					    	type='text'
					    	className='StatusElements'
					    	onChange={this._handleChange}
					    	placeholder='Search'
					    />
						</div>

						{item}

						<div className='CircleIconPlus' onClick={this._addItem}>
							<i className='icon-plus'></i>
						</div>
					</section>
				</div>

			</div>
		)
	}
}